export const getUserDocuments = (
  userList: UserInterface[],
  user_id: String
): { user: UserInterface; document: UserDocumentsInterface[] } | null => {
  const thisUser = userList.find((item) => item.user_id === user_id);
  if (thisUser) {
    const thisDoc = thisUser.details.filter(
      (item) => item.validation_status !== "waiting"
    );
    return { user: thisUser, document: thisDoc };
  }
  return null;
};

export const adminDisplayClaimTitle = (
  documentCategories: DocumentReducerInterface,
  candidateDocuments: UserDocumentsInterface[],
  thisClaim: CandidateClaimInterface
): string => {
  let result: string = "";

  // CHECK IF CLAIM IS FOR DOCUMENTS ===============================================
  if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
    let thisDoc = candidateDocuments.find(
      (item) => item.doc_id === thisClaim.claiming_on_id
    );
    result = thisDoc
      ? AdminClaimGetDocTitle(documentCategories, thisDoc).main
      : "Document not found";
  }
  // END CHECK IF CLAIM IS FOR DOCUMENTS ===============================================

  return result;
};

export const AdminClaimGetDocTitle = (
  documentCategories: DocumentReducerInterface,
  doc: UserDocumentsInterface
): { category: string; sub_category: string; field: string; main: string } => {
  let DocCategory: string = "",
    DocSubCategory: string = "",
    DocField: string = "",
    DocTitle: string = "";
  if (
    doc.sub_category_id &&
    doc.sub_category_id.length > 0 &&
    documentCategories.documentSubCategory
  ) {
    DocTitle = documentCategories.documentSubCategory.filter(
      (c) => c.user_detail_subcategory_id === doc.sub_category_id
    )[0].title;
    DocSubCategory = DocTitle;
  } else if (
    doc.category_id.length > 0 &&
    documentCategories.documentCategory
  ) {
    DocTitle = documentCategories.documentCategory.filter(
      (c) => c.user_detail_category_id === doc.category_id
    )[0].title;
    DocCategory = DocTitle;
  }
  if (
    doc.category_subcategory_data.length > 0 &&
    documentCategories.documentFields
  ) {
    DocField = documentCategories.documentFields.filter(
      (c) => c._id === doc.category_subcategory_data
    )[0].title;
    DocTitle += " in " + DocField;
  }

  return {
    category: DocCategory,
    sub_category: DocSubCategory,
    field: DocField,
    main: DocTitle,
  };
};

export const getApplicationById = (
  allApplications: allClaimApplicationInterface[] | null,
  systemPositions: PositionAPI[] | null,
  application_id: String
): {
  application: allClaimApplicationInterface;
  position: PositionAPI;
} | null => {
  if (allApplications) {
    let thisApplication = allApplications.find(
      (item) => item._id === application_id
    );
    if (thisApplication && systemPositions) {
      let thisPosition = systemPositions.find(
        (item) => item.position_id === thisApplication!.position
      );
      if (thisPosition) {
        return { application: thisApplication, position: thisPosition };
      }
    }
  }
  return null;
};

type AppProps = {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  userList: UserInterface[];
  systemPositions: PositionAPI[] | null;
  getPositions: Function;
  getAllApplications: Function;
  getAllClaims: Function;
  GetAllSessionPlans: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  verifyAllDocument: Function;
  fetchUserAccess: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

const Index: React.FC<AppProps> = (props) => {
  const [thisPeriod, setThisPeriod] = useState<SessionPlanInterface | null>(
    null
  );
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingSession, setLoadingSession] = useState<boolean>(true);
  const [periodChanged, setPeriodChanged] = useState<boolean>(false);
  const [loadingPosition, setLoadingPosition] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] = useState<boolean>(true);
  const [loadingClaims, setLoadingClaims] = useState<boolean>(true);
  const [loadingDocuments, setLoadingDocuments] = useState<boolean>(true);
  const [loadingClarifications, setLoadingClarifications] =
    useState<boolean>(true);
  const [loadingUsers, setLoadingUsers] = useState(true);
  const [loadingFields, setLoadingFields] = useState<boolean>(true);
  const [viewClaim, setViewClaim] = useState<boolean>(false);
  const [claimId, setClaimId] = useState<string | null>(null);
  const [claimType, setClaimType] = useState<string | null>(null);
  const [candidateId, setCandidateId] = useState<string | null>(null);

  // FOR FORM ================
  const [viewImage, setViewImage] = useState<boolean>(true);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [actionTaken, setActionTaken] = useState<ClaimDecisionStatus | null>(
    null
  );
  const [responseMsg, setResponseMsg] = useState<string>("");
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);

  useEffect(() => {
    // LOADING APPLICATION SESSION ====================================
    if (props.application_sessions === null) {
      props.GetAllSessionPlans((status: boolean, msg: string) => {
        if (!status) {
          setLoadingSession(false);
          setLoading(false);
          if (msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    } else if (loadingSession) {
      setLoading(false);
      setLoadingSession(false);
    }

    // SETTING ACTIVE SESSION ==========================
    if (thisPeriod === null && !loadingSession && props.application_sessions) {
      let thisSession = props.application_sessions.find(
        (item) => item.is_active === true
      );
      setThisPeriod(
        thisSession
          ? thisSession
          : props.application_sessions[props.application_sessions.length - 1]
      );
    }

    // LOADING DOCUMENT CATEGORIES ==========================
    if (!props.documentReducer.documentCategory && loadingDocuments) {
      props.getDocumentCategory();
      setLoadingDocuments(false);
    } else if (loadingDocuments) {
      setLoadingDocuments(false);
    }
    // LOADING DOCUMENT CLARIFICATIONS ==========================
    if (!props.documentReducer.documentSubCategory && loadingClarifications) {
      props.getDocumentSubCategory();
      setLoadingClarifications(false);
    } else if (loadingClarifications) {
      setLoadingClarifications(false);
    }
    // LOADING DOCUMENT FIELDS ==========================
    if (!props.documentReducer.documentFields && loadingFields) {
      props.getDocumentFields();
      setLoadingFields(false);
    } else if (loadingFields) {
      setLoadingFields(false);
    }

    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (props.claiming.allClaims === null && thisPeriod !== null) {
      props.getAllClaims(
        2,
        thisPeriod.session_plan_id,
        (status: boolean, msg: string) => {
          !status && setLoadingClaims(status);
          if (status === false && msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (thisPeriod && loadingClaims) {
      setLoadingClaims(false);
    }

    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (props.claiming.allApplications === null && thisPeriod !== null) {
      props.getAllApplications(
        thisPeriod.session_plan_id,
        (status: boolean, msg: string) => {
          !status && setLoadingApplications(status);
          if (status === false && msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (thisPeriod && loadingApplications) {
      setLoadingApplications(false);
    }

    if (!props.userList.length && loadingUsers) {
      setLoadingUsers(false);
      props.fetchUserAccess();
    } else if (loadingUsers) {
      setLoadingUsers(false);
    }

    if (props.systemPositions === null) {
      props.getPositions((status: boolean, msg: string) => {
        setLoadingPosition(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (loadingPosition) {
      setLoadingPosition(false);
    }
  }, [
    loadingApplications,
    loadingClaims,
    loadingClarifications,
    loadingDocuments,
    loadingFields,
    loadingPosition,
    loadingSession,
    loadingUsers,
    props,
    thisPeriod,
  ]);

  if (loading || props.application_sessions === null) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }

  const displayClaimName = (
    thisClaim: CandidateClaimInterface
  ): string | JSX.Element => {
    let result: string = thisClaim.claiming_on_id;
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      if (loadingUsers)
        return <div className={`h-6 bg-gray-300 rounded w-2/3`}></div>;
      result = getUserDocuments(props.userList, thisClaim.user)
        ? getUserDocuments(props.userList, thisClaim.user)!.user.user_info
            .fname +
          " " +
          getUserDocuments(props.userList, thisClaim.user)!.user.user_info.lname
        : "-";
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (
        loadingApplications ||
        loadingPosition ||
        props.systemPositions === null ||
        props.claiming.allApplications === null
      ) {
        return <div className={`h-6 bg-gray-300 rounded w-2/3`}></div>;
      } else if (props.claiming.allApplications) {
        result = getUserDocuments(props.userList, thisClaim.user)
          ? getUserDocuments(props.userList, thisClaim.user)!.user.user_info
              .fname +
            " " +
            getUserDocuments(props.userList, thisClaim.user)!.user.user_info
              .lname
          : "-";
      }
    }

    return result;
  };

  const displayClaimTitle = (
    thisClaim: CandidateClaimInterface
  ): string | JSX.Element => {
    let result: string = thisClaim.claiming_on_id;
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      if (loadingUsers)
        return <div className={`h-6 bg-gray-300 rounded w-2/3`}></div>;
      result = getUserDocuments(props.userList, thisClaim.user)
        ? adminDisplayClaimTitle(
            props.documentReducer,
            getUserDocuments(props.userList, thisClaim.user)!.document,
            thisClaim
          )
        : "Document not found";
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (loadingApplications || loadingPosition) {
        return <div className={`h-6 bg-gray-300 rounded w-2/3`}></div>;
      } else if (props.claiming.allApplications) {
        let thisApplication = props.claiming.allApplications.find(
          (item) => item._id === thisClaim.claiming_on_id
        );
        if (
          thisApplication &&
          props.claiming.allApplications &&
          props.systemPositions
        ) {
          let thisPosition = props.systemPositions.find(
            (item) => item.position_id === thisApplication!.position
          );
          if (thisPosition) {
            result = thisPosition.position_title;
          } else {
            result = "Application not found";
          }
        }
      }
    }

    return result;
  };

  const tableData = (): JSX.Element[] => {
    if (
      props.claiming.allClaims &&
      props.claiming.claimTypes &&
      !loadingDocuments &&
      !loadingSession &&
      !loadingUsers &&
      !loadingClaims
    ) {
      // CHECK IF THERE IS NO CLAIM ========================================
      if (
        filterClaimsByClaimType(
          props.claiming.allClaims,
          props.claiming.selectedClaimType
        ).length <= 0
      ) {
        return [
          <tr
            className="border-b border-gray-200 group hover:bg-gray-50 cursor-pointer"
            key="key3201"
          >
            <td className="px-3 py-1">
              <NoResult
                theme={NoResultThemes.Light}
                value={"No claim found!"}
              />
            </td>
          </tr>,
        ];
      }
      // END CHECK IF THERE IS NO CLAIM ========================================

      return filterClaimsByClaimType(
        props.claiming.allClaims,
        props.claiming.selectedClaimType
      ).map((claim, index) => (
        <tr
          className="border-b border-gray-200 group hover:bg-gray-50 cursor-pointer"
          key={index + claim.id}
          onClick={() => {
            setViewClaim(true);
            setClaimId(claim.id);
            setCandidateId(claim.user);
            setClaimType(whichClaimType(claim) ? whichClaimType(claim)! : null);
          }}
        >
          <td className="px-3 py-1">{index + 1}</td>
          <td className="px-3 py-1 whitespace-nowrap">
            {displayClaimName(claim)}
          </td>
          <td className="px-3 py-1 whitespace-nowrap">
            {displayClaimTitle(claim)}
          </td>
          <td className="px-3 py-1 text-center whitespace-nowrap">
            {whichClaimType(claim) ? whichClaimType(claim)! : "-"}
          </td>
          <td className="px-3 py-1 text-center whitespace-nowrap">
            <span
              className={`inline-block rounded-full duration-300 text-xs font-semibold px-3 py-1 ${
                whichClaimStatus(claim).classes
              }`}
            >
              {whichClaimStatus(claim).status}
            </span>
          </td>
          <td className="px-3 py-1 text-center whitespace-nowrap">
            <button className="btn bg-blue-500 text-white group-hover:bg-blue-600">
              View
            </button>
          </td>
        </tr>
      ));
    }

    return [
      <tr key={43243} className="border-b border-gray-200">
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
      </tr>,
      <tr key={43243343} className="border-b border-gray-200">
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
      </tr>,
      <tr key={4327543} className="border-b border-gray-200">
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
      </tr>,
    ];
  };

  const onPeriodChange = (period_id: string) => {
    setPeriodChanged(true);
    props.getAllClaims(2, period_id, (status: boolean, msg: string) => {
      !status && setPeriodChanged(false);
      if (status === false && msg.length) {
        props.PushPopupAlert({
          title: msg,
          theme: PopupAlertTheme.danger,
        });
      }
    });
  };

  return (
    <Fragment>
      {/* {props.claiming.myClaims} */}
      {/* SIDE NAVIGATION */}
      {!viewClaim && (
        <SideNavigation>
          {/* FILTER SECTION */}
          <AdminFilterByType loadingClaims={loadingClaims} />
          {/* END FILTER SECTION */}
        </SideNavigation>
      )}
      {/* END SIDE NAVIGATION */}

      {/* CLAIM TITLE ===================================== */}
      {!viewClaim && (
        <ClaimHeaderTitle
          headerTitle={`${
            props.claiming.selectedClaimType === "all"
              ? "All"
              : props.claiming.selectedClaimType ===
                PlansTypeKeys.DOCUMENT_VALIDATION
              ? "Documents"
              : props.claiming.selectedClaimType === PlansTypeKeys.APPLICATION
              ? "Applications"
              : props.claiming.selectedClaimType === PlansTypeKeys.EXAMINATION
              ? "Exam"
              : props.claiming.selectedClaimType ===
                PlansTypeKeys.MARKS_PUBLICATION
              ? "Marks publication"
              : props.claiming.selectedClaimType ===
                PlansTypeKeys.PLACEMENT_TRANSFER
              ? "Placements"
              : ""
          } Claiming`}
          loading={loading}
          loadingSession={loadingSession}
          thisPeriod={thisPeriod}
          setThisPeriod={setThisPeriod}
          setLoadingSession={setLoadingSession}
          application_sessions={props.application_sessions}
          onPeriodChange={onPeriodChange}
          periodChanged={periodChanged}
        />
      )}
      {/* END CLAIM TITLE ===================================== */}

      {!viewClaim && (
        <section className="bg-white w-full p-1">
          <Table
            columns={[
              { columnTitle: "", alignText: "left", padding: "p-3" },
              { columnTitle: "", alignText: "left", padding: "p-3" },
              {
                columnTitle: "",
                alignText: "left",
                padding: "p-3",
              },
              {
                columnTitle: "",
                alignText: "left",
                padding: "p-3",
              },
              { columnTitle: "", alignText: "left", padding: "p-3" },
            ]}
            hideColumns={true}
            rows={tableData}
            pagination={true}
            search={true}
            thColor="gray"
            loading={loadingClaims}
          />
        </section>
      )}

      {/* view claim */}
      {viewClaim &&
        candidateId &&
        getUserDocuments(props.userList, candidateId) &&
        props.claiming.allClaims &&
        claimId &&
        getClaimById(props.claiming.allClaims, claimId) &&
        props.claiming.claimTypes && (
          <section className="bg-white w-full p-1 my-4">
            {/* VIEW CLAIM HEADER =============================== */}
            <section className="flex items-center gap-2 py-2 border-b">
              <button
                className={`ml-3 bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                  loadingRequest ? "cursor-not-allowed" : "cursor-pointer"
                } animate__animated animate__fadeIn`}
                disabled={loadingRequest}
                onClick={() => {
                  setViewClaim(false);
                  setViewImage(true);
                  setLoadingRequest(false);
                  setActionTaken(null);
                  setResponseMsg("");
                  setFormError(null);
                }}
              >
                <MdArrowBack className="w-7 h-7" />
              </button>
              {/* <div className={`text-blue-600 font-extrabold text-2xl px-2`}>
                Positions
              </div> */}
            </section>
            {/* END VIEW CLAIM HEADER =============================== */}

            <section className="my-2">
              {/* FOR DOCUMENT ========================== */}
              {claimType &&
                claimType.toLocaleLowerCase() ===
                  ClaimTypesInterface.DOCUMENT.toLocaleLowerCase() && (
                  <div>
                    {getUserDocuments(
                      props.userList,
                      candidateId
                    )!.document.find(
                      (item) =>
                        item.doc_id ===
                        getClaimById(props.claiming.allClaims!, claimId)!
                          .claiming_on_id
                    ) ? (
                      <AdminClaimDocView
                        thisDoc={
                          getUserDocuments(
                            props.userList,
                            candidateId
                          )!.document.find(
                            (item) =>
                              item.doc_id ===
                              getClaimById(props.claiming.allClaims!, claimId)!
                                .claiming_on_id
                          )!
                        }
                        documentCategories={props.documentReducer}
                        thisClaim={
                          getClaimById(props.claiming.allClaims, claimId)!
                        }
                        userList={props.userList}
                        claimTypes={props.claiming.claimTypes}
                        viewImage={viewImage}
                        setViewImage={setViewImage}
                        loadingRequest={loadingRequest}
                        setLoadingRequest={setLoadingRequest}
                        actionTaken={actionTaken}
                        setActionTaken={setActionTaken}
                        responseMsg={responseMsg}
                        setResponseMsg={setResponseMsg}
                        formError={formError}
                        setFormError={setFormError}
                        candidateId={candidateId}
                      />
                    ) : (
                      <NoResult
                        theme={NoResultThemes.Dark}
                        value={"Document not found"}
                      />
                    )}
                  </div>
                )}

              {/* FOR APPLICATION ========================== */}
              {claimType &&
                claimType.toLocaleLowerCase() ===
                  ClaimTypesInterface.APPLICATION.toLocaleLowerCase() && (
                  <div>
                    {getApplicationById(
                      props.claiming.allApplications,
                      props.systemPositions,
                      getClaimById(props.claiming.allClaims, claimId)!
                        .claiming_on_id
                    ) ? (
                      props.systemPositions && (
                        <AdminClaimAppView
                          thisApplication={
                            getApplicationById(
                              props.claiming.allApplications,
                              props.systemPositions,
                              getClaimById(props.claiming.allClaims, claimId)!
                                .claiming_on_id
                            )!.application
                          }
                          thisPosition={
                            getApplicationById(
                              props.claiming.allApplications,
                              props.systemPositions,
                              getClaimById(props.claiming.allClaims, claimId)!
                                .claiming_on_id
                            )!.position
                          }
                          documentCategories={props.documentReducer}
                          thisClaim={
                            getClaimById(props.claiming.allClaims, claimId)!
                          }
                          userList={props.userList}
                          claimTypes={props.claiming.claimTypes}
                          systemPositions={props.systemPositions}
                        />
                      )
                    ) : (
                      <NoResult
                        theme={NoResultThemes.Dark}
                        value={"Application not found"}
                      />
                    )}
                  </div>
                )}
            </section>
          </section>
        )}
      {/* end view claim */}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
  claiming,
  document,
  users,
}: StoreState): {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  userList: UserInterface[];
} => {
  return {
    auth: auth,
    application_sessions: sessionPlan.allSessionPlans,
    systemPositions: system_settings.systemPositions,
    claiming,
    documentReducer: document,
    userList: users,
  };
};

