import React, { Fragment, useEffect, useState } from "react";
import { FaBookOpen } from "react-icons/fa";
import { connect } from "react-redux";
import {
  Auth,
  schoolLevelsStoreInterface,
  loadEducationCategories,
  PushPopupAlert,
} from "../../../actions";
import {
  getAllClaims,
  GetClaimingTypes,
  getAllApplications,
  setSelectedClaimType,
} from "../../../actions/claiming";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
} from "../../../actions/document";
import { GetAllSessionPlans } from "../../../actions/session";
import { getPositions } from "../../../actions/settings";
import { fetchUserAccess } from "../../../actions/users";
import { PopupAlertData } from "../../../components/Alert/Popup";
import NoResult, {
  NoResultThemes,
} from "../../../components/NoResult/NoResult";
import {
  CandidateClaimInterface,
  ClaimDecisionStatus,
  ClaimToPrintInterface,
  ClaimTypesInterfaceAPI,
  education_category,
  PlansTypeKeys,
  PositionAPI,
  SessionPlanInterface,
} from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { ClaimingReducerType } from "../../../reducers/claiming";
import { DocumentReducerInterface } from "../../../reducers/document";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import { UserInterface } from "../../../shared/interfaces";
import { DATE } from "../../../utils/functions";
import {
  adminDisplayClaimTitle,
  getUserDocuments,
} from "../../Claimings/Admin";
import { whichClaimType } from "../../Claimings/Candidate";
import PieChart from "../ChartComponents/PieChart";
import ExamDashboardLabels from "../ExamDashboard/ExamDashboardLabels";
import AllClaimChart from "./AllClaimChart";
import ClaimDashBoardTitle from "./ClaimDashBoardTitle";

interface WaitingListDashboardProps {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  userList: UserInterface[];
  systemPositions: PositionAPI[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  setSelectedClaimType: Function;
  getPositions: Function;
  getAllClaims: Function;
  GetClaimingTypes: Function;
  getAllApplications: Function;
  GetAllSessionPlans: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  fetchUserAccess: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const _ClaimDashboard: React.FC<WaitingListDashboardProps> = (props) => {
  const [thisPeriod, setThisPeriod] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingSession, setLoadingSession] = useState<boolean>(true);
  const [periodChanged, setPeriodChanged] = useState<boolean>(false);
  const [loadingPosition, setLoadingPosition] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] = useState<boolean>(true);
  const [loadingClaims, setLoadingClaims] = useState<boolean>(true);
  const [loadingDocuments, setLoadingDocuments] = useState<boolean>(true);
  const [loadingInstitutes, setLoadingInstitutes] = useState<boolean>(true);
  const [loadingClarifications, setLoadingClarifications] =
    useState<boolean>(true);
  const [loadingUsers, setLoadingUsers] = useState(true);
  const [loadingFields, setLoadingFields] = useState<boolean>(true);
  const [thisEducation, setThisEducation] = useState<education_category | null>(
    null
  );
  const [thisLabel, setThisLabel] = useState<string>("All claim types status");

  useEffect(() => {
    // LOADING EDUCATION CATEGORY =================================
    if (
      props.schoolLevels.education_categories === null ||
      props.schoolLevels.education_categories.length === 0
    ) {
      props.loadEducationCategories(setLoadingInstitutes);
    } else {
      setLoadingInstitutes(false);
    }
    // END LOADING EDUCATION CATEGORY =================================
    // LOADING CLAIM TYPES ========================================
    if (props.claiming.claimTypes === null) {
      props.GetClaimingTypes((status: boolean, msg: string) => {
        if (status === false) {
          if (msg.length > 0) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }
    // END LOADING CLAIM TYPES ========================================
    // LOADING APPLICATION SESSION ====================================
    if (props.application_sessions === null) {
      props.GetAllSessionPlans((status: boolean, msg: string) => {
        if (!status) {
          setLoadingSession(false);
          setLoading(false);
          if (msg.length) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    } else if (loadingSession) {
      setLoading(false);
      setLoadingSession(false);
    }

    // SETTING ACTIVE SESSION ==========================
    if (
      isEmptyOrSpaces(thisPeriod) &&
      !loadingSession &&
      props.application_sessions
    ) {
      let thisSession = props.application_sessions.find(
        (item) => item.is_active === true
      );
      setThisPeriod(
        thisSession
          ? thisSession.session_plan_id
          : props.application_sessions[props.application_sessions.length - 1]
              .session_plan_id
      );
    }

    // LOADING DOCUMENT CATEGORIES ==========================
    if (!props.documentReducer.documentCategory && loadingDocuments) {
      props.getDocumentCategory();
      setLoadingDocuments(false);
    } else if (loadingDocuments) {
      setLoadingDocuments(false);
    }
    // LOADING DOCUMENT CLARIFICATIONS ==========================
    if (!props.documentReducer.documentSubCategory && loadingClarifications) {
      props.getDocumentSubCategory();
      setLoadingClarifications(false);
    } else if (loadingClarifications) {
      setLoadingClarifications(false);
    }
    // LOADING DOCUMENT FIELDS ==========================
    if (!props.documentReducer.documentFields && loadingFields) {
      props.getDocumentFields();
      setLoadingFields(false);
    } else if (loadingFields) {
      setLoadingFields(false);
    }

    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (props.claiming.allClaims === null && !isEmptyOrSpaces(thisPeriod)) {
      props.getAllClaims(2, thisPeriod, (status: boolean, msg: string) => {
        !status && setLoadingClaims(status);
        if (status === false && msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (!isEmptyOrSpaces(thisPeriod) && loadingClaims) {
      setLoadingClaims(false);
    }

    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (
      props.claiming.allApplications === null &&
      !isEmptyOrSpaces(thisPeriod)
    ) {
      props.getAllApplications(thisPeriod, (status: boolean, msg: string) => {
        !status && setLoadingApplications(status);
        if (status === false && msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (!isEmptyOrSpaces(thisPeriod) && loadingApplications) {
      // alert("apps");
      setLoadingApplications(false);
    }

    if (!props.userList.length && loadingUsers) {
      setLoadingUsers(false);
      props.fetchUserAccess();
    } else if (loadingUsers) {
      setLoadingUsers(false);
    }

    if (props.systemPositions === null) {
      props.getPositions((status: boolean, msg: string) => {
        setLoadingPosition(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (loadingPosition) {
      setLoadingPosition(false);
    }
  }, [
    loadingApplications,
    loadingClaims,
    loadingClarifications,
    loadingDocuments,
    loadingFields,
    loadingPosition,
    loadingSession,
    loadingUsers,
    props,
    thisPeriod,
  ]);

  const onPeriodChange = (period_id: string) => {
    setPeriodChanged(true);
    props.getAllClaims(2, period_id, (status: boolean, msg: string) => {
      !status && setPeriodChanged(false);
      if (status === false && msg.length) {
        props.PushPopupAlert({
          title: msg,
          theme: PopupAlertTheme.danger,
        });
      }
    });
  };

  const getClaimInstitute = (
    thisClaim: CandidateClaimInterface
  ): string | null => {
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      return null;
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (props.claiming.allApplications) {
        let thisApplication = props.claiming.allApplications.find(
          (item) => item._id === thisClaim.claiming_on_id
        );
        if (
          thisApplication &&
          props.claiming.allApplications &&
          props.systemPositions
        ) {
          // return thisApplication.
          let thisPosition = props.systemPositions.find(
            (item) => item.position_id === thisApplication!.position
          );
          if (thisPosition) {
            return thisPosition.education_institute_id.education_institute_id;
          }
        }
      }
    }

    return null;
  };

  const displayClaimName = (
    thisClaim: CandidateClaimInterface
  ): UserInterface | null => {
    let result: UserInterface | null = null;
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      result = getUserDocuments(props.userList, thisClaim.user)
        ? getUserDocuments(props.userList, thisClaim.user)!.user
        : null;
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (props.claiming.allApplications) {
        result = getUserDocuments(props.userList, thisClaim.user)
          ? getUserDocuments(props.userList, thisClaim.user)!.user
          : null;
      }
    }

    return result;
  };

  const displayClaimTitle = (thisClaim: CandidateClaimInterface): string => {
    let result: string = thisClaim.claiming_on_id;
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      result = getUserDocuments(props.userList, thisClaim.user)
        ? adminDisplayClaimTitle(
            props.documentReducer,
            getUserDocuments(props.userList, thisClaim.user)!.document,
            thisClaim
          )
        : "Document not found";
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (props.claiming.allApplications) {
        let thisApplication = props.claiming.allApplications.find(
          (item) => item._id === thisClaim.claiming_on_id
        );
        if (
          thisApplication &&
          props.claiming.allApplications &&
          props.systemPositions
        ) {
          let thisPosition = props.systemPositions.find(
            (item) => item.position_id === thisApplication!.position
          );
          if (thisPosition) {
            result = thisPosition.position_title;
          } else {
            result = "Application not found";
          }
        }
      }
    }

    return result;
  };

  const ClaimTypes = (
    claims: ClaimToPrintInterface[],
    claimsTypes: ClaimTypesInterfaceAPI[]
  ): {
    title: string;
    count: {
      all: number;
      accepted: number;
      rejected: number;
      pending: number;
    };
    type: string | "all";
  }[] => {
    let output: {
      title: string;
      count: {
        all: number;
        accepted: number;
        rejected: number;
        pending: number;
      };
      type: string | "all";
    }[] = [
      {
        title: "All claim types status",
        count: {
          all: claims.length,
          accepted: claims.filter(
            (item) => item.decision === ClaimDecisionStatus.ACCEPTED
          ).length,
          rejected: claims.filter(
            (item) => item.decision === ClaimDecisionStatus.REJECTED
          ).length,
          pending: claims.filter((item) => item.decision === "PENDING").length,
        },
        type: "all",
      },
    ];
    for (let i in claimsTypes) {
      output.push({
        title: claimsTypes[i].title + " claim status",
        count: {
          all: claims.filter((itm) => itm.claim_type === claimsTypes[i].title)
            .length,
          accepted: claims.filter(
            (itm) =>
              itm.claim_type === claimsTypes[i].title &&
              itm.decision === ClaimDecisionStatus.ACCEPTED
          ).length,
          rejected: claims.filter(
            (itm) =>
              itm.claim_type === claimsTypes[i].title &&
              itm.decision === ClaimDecisionStatus.REJECTED
          ).length,
          pending: claims.filter(
            (itm) =>
              itm.claim_type === claimsTypes[i].title &&
              itm.decision === "PENDING"
          ).length,
        },
        type: claimsTypes[i].title,
      });
    }
    return output;
  };

  const FC_ClaimData = (
    allClaims: CandidateClaimInterface[] | null,
    claimTypes: ClaimTypesInterfaceAPI[] | null
  ): ClaimToPrintInterface[] | null => {
    if (allClaims === null || claimTypes === null || allClaims.length <= 0)
      return null;
    let arrayNew: ClaimToPrintInterface[] = [];
    let n: number = 1;
    // LOOP IN ALL CLAIM ONE BY ONE ###########################
    for (const key in allClaims) {
      if (Object.prototype.hasOwnProperty.call(allClaims, key)) {
        // LOOP IN ALL CLAIM OF ONE FILE ONE BY ONE ===========================
        for (const keyClaim in allClaims[key].claims) {
          if (
            Object.prototype.hasOwnProperty.call(
              allClaims[key].claims,
              keyClaim
            )
          ) {
            const element = allClaims[key].claims[keyClaim];
            const thisUser = displayClaimName(allClaims[key]);
            const thisType = whichClaimType(allClaims[key]);
            let dataArray: ClaimToPrintInterface = {
              claim_id: n,
              names: thisUser
                ? thisUser.user_info.fname + " " + thisUser.user_info.lname
                : "",
              gender: thisUser
                ? thisUser!.user_info.sex
                  ? "Male"
                  : "Female"
                : "",
              national_id: thisUser ? thisUser!.n_id! : "",
              phone: thisUser ? thisUser!.phone_number : "",
              email: thisUser ? thisUser!.email : "",
              claim_type: thisType ? thisType : "",
              claim_level: element.level,
              education_category: getClaimInstitute(allClaims[key]),
              claim_session: thisPeriod,
              date: DATE(element.createdAt),
              decision: element.decision_taken_status
                ? element.decision_taken_status
                : "PENDING",
              claim_title: displayClaimTitle(allClaims[key]),
            };
            arrayNew.push(dataArray);
            n++;
          }
        }
      }
    }
    return arrayNew;
  };

  const getClaimDataByTitle = (
    allData: {
      title: string;
      count: {
        all: number;
        accepted: number;
        rejected: number;
        pending: number;
      };
      type: string | "all";
    }[]
  ):
    | {
        title: string;
        count: {
          all: number;
          accepted: number;
          rejected: number;
          pending: number;
        };
        type: string | "all";
      }
    | undefined => {
    return allData.find((item) => item.title === thisLabel);
  };

  if (loading || props.application_sessions === null) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }
  return (
    <Fragment>
      {/* HEADER TITLE */}
      <ClaimDashBoardTitle
        education_categories={props.schoolLevels.education_categories}
        loading={loading}
        loadingApplications={loadingApplications}
        loadingEducation={loadingInstitutes}
        thisEducation={thisEducation}
        setThisEducation={setThisEducation}
        thisPeriod={thisPeriod}
        setThisPeriod={setThisPeriod}
        application_sessions={props.application_sessions}
        reportTitle={"Claim Dashboard"}
        allEducation={true}
        periodChanged={periodChanged}
        onPeriodChange={onPeriodChange}
      />
      {/* END HEADER TITLE */}

      {/* CLAIM ALL STATUS LABELS ======================================= */}
      {props.claiming.claimTypes &&
        props.claiming.allClaims &&
        FC_ClaimData(props.claiming.allClaims, props.claiming.claimTypes) && (
          <section className="my-2">
            <div className="text-2xl font-semibold text-gray-600">
              {thisLabel}{" "}
              <select
                className={`bg-white text-gray-600 border-2 border-blue-600 rounded-lg px-2 py-1 focus:outline-none`}
                value={thisLabel}
                onChange={(e) => setThisLabel(e.target.value)}
              >
                {ClaimTypes(
                  FC_ClaimData(
                    props.claiming.allClaims,
                    props.claiming.claimTypes
                  )!,
                  props.claiming.claimTypes!
                ).map((item, index: number) => (
                  <option key={item.type + index} value={item.title}>
                    {capitalizeFirstLetter(item.type.toLocaleLowerCase())}
                  </option>
                ))}
              </select>
            </div>
            {getClaimDataByTitle(
              ClaimTypes(
                FC_ClaimData(
                  props.claiming.allClaims,
                  props.claiming.claimTypes
                )!,
                props.claiming.claimTypes!
              )
            ) && (
              <ExamDashboardLabels
                dataLabels={[
                  {
                    title: "Total claim",
                    icon: FaBookOpen,
                    iconColor: "blue",
                    count: getClaimDataByTitle(
                      ClaimTypes(
                        FC_ClaimData(
                          props.claiming.allClaims,
                          props.claiming.claimTypes
                        )!,
                        props.claiming.claimTypes!
                      )
                    )!.count.all,
                  },
                  {
                    title: "Accepted Claim",
                    icon: FaBookOpen,
                    iconColor: "green",
                    count: getClaimDataByTitle(
                      ClaimTypes(
                        FC_ClaimData(
                          props.claiming.allClaims,
                          props.claiming.claimTypes
                        )!,
                        props.claiming.claimTypes!
                      )
                    )!.count.accepted,
                  },
                  {
                    title: "Rejected Claim",
                    icon: FaBookOpen,
                    iconColor: "yellow",
                    count: getClaimDataByTitle(
                      ClaimTypes(
                        FC_ClaimData(
                          props.claiming.allClaims,
                          props.claiming.claimTypes
                        )!,
                        props.claiming.claimTypes!
                      )
                    )!.count.rejected,
                  },
                  {
                    title: "Pending Claim",
                    icon: FaBookOpen,
                    iconColor: "gray",
                    count: getClaimDataByTitle(
                      ClaimTypes(
                        FC_ClaimData(
                          props.claiming.allClaims,
                          props.claiming.claimTypes
                        )!,
                        props.claiming.claimTypes!
                      )
                    )!.count.pending,
                  },
                ]}
                labelsOnRow={5}
              >
                <div className="flex justify-center">
                  <PieChart
                    options={{
                      labels: ["Accepted", "Rejected", "Pending"],
                      legend: {
                        show: false,
                        position: "bottom",
                        horizontalAlign: "center",
                        fontSize: "16px",
                        itemMargin: {
                          horizontal: 3,
                          vertical: 0,
                        },
                      },
                      plotOptions: {
                        pie: {
                          donut: {
                            labels: {
                              show: true,
                              name: {
                                show: true,
                                fontSize: "16px",
                                //   fontFamily: "Rubik",
                                color: "#2564eb",
                                offsetY: -10,
                              },
                              value: {
                                show: true,
                                fontSize: "20px",
                                fontFamily: "Helvetica, Arial, sans-serif",
                                color: "#2564eb",
                                offsetY: 10,
                                formatter: function (val) {
                                  return val;
                                },
                              },
                              total: {
                                show: true,
                                label: "Total",
                                color: "#2564eb",
                                formatter: function (w) {
                                  return w.globals.seriesTotals.reduce(
                                    (a: number, b: number) => {
                                      return a + b;
                                    },
                                    0
                                  );
                                },
                              },
                            },
                          },
                        },
                      },
                      colors: ["#469769", "#d97724", "#4b5563"],
                    }}
                    series={[
                      getClaimDataByTitle(
                        ClaimTypes(
                          FC_ClaimData(
                            props.claiming.allClaims,
                            props.claiming.claimTypes
                          )!,
                          props.claiming.claimTypes!
                        )
                      )!.count.accepted,
                      getClaimDataByTitle(
                        ClaimTypes(
                          FC_ClaimData(
                            props.claiming.allClaims,
                            props.claiming.claimTypes
                          )!,
                          props.claiming.claimTypes!
                        )
                      )!.count.rejected,
                      getClaimDataByTitle(
                        ClaimTypes(
                          FC_ClaimData(
                            props.claiming.allClaims,
                            props.claiming.claimTypes
                          )!,
                          props.claiming.claimTypes!
                        )
                      )!.count.pending,
                    ]}
                    width={210}
                  />
                </div>
              </ExamDashboardLabels>
            )}
          </section>
        )}
      {props.claiming.claimTypes &&
        props.claiming.allClaims &&
        !FC_ClaimData(props.claiming.allClaims, props.claiming.claimTypes) &&
        props.claiming.allClaims &&
        props.claiming.allClaims.length <= 0 && (
          <div className="my-4">
            <NoResult
              theme={NoResultThemes.Dark}
              value={"No claims data found!"}
            />
          </div>
        )}
      {/* CLAIM STATUS ON COUNTRY LEVEL LABELS ======================================= */}

      {/* ===================================================================================== */}

      {/* EXAM PERFORMANCE ON COUNTRY CHART ======================================= */}
      {props.claiming.claimTypes &&
        FC_ClaimData(props.claiming.allClaims, props.claiming.claimTypes) && (
          <AllClaimChart
            ClaimTypes={ClaimTypes(
              FC_ClaimData(
                props.claiming.allClaims,
                props.claiming.claimTypes
              )!,
              props.claiming.claimTypes
            )}
          />
        )}
      {/* END EXAM PERFORMANCE ON COUNTRY CHART ======================================= */}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
  claiming,
  document,
  users,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  userList: UserInterface[];
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return {
    auth: auth,
    application_sessions: sessionPlan.allSessionPlans,
    systemPositions: system_settings.systemPositions,
    claiming,
    documentReducer: document,
    userList: users,
    schoolLevels,
  };
};

export const ClaimDashboard = connect(mapStateToProps, {
  getAllClaims,
  GetClaimingTypes,
  getAllApplications,
  GetAllSessionPlans,
  loadEducationCategories,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  setSelectedClaimType,
  fetchUserAccess,
  getPositions,
  PushPopupAlert,
})(_ClaimDashboard);
