/**
 * * ****************************** INTERFACES *****************************
 */

// CANDIDATE_APPLICATIONS
export interface GetCandidateApplicationsAction {
  type: ActionTypes.GET_CANDIDATES_APPLICATIONS;
  payload: claimApplicationsInterface[];
}

// CREATE PERSONAL CLAIM
export interface CreateClaimAction {
  type: ActionTypes.CREATE_CLAIM;
  payload: CandidateClaimInterface;
}

// EVALUATE CANDIDATE CLAIM
export interface EvaluateClaimAction {
  type: ActionTypes.EVALUATE_CLAIM;
  payload: CandidateClaimInterface;
}

// CREATE PERSONAL CLAIM
export interface CreateReClaimAction {
  type: ActionTypes.RE_CLAIM;
  payload: CandidateClaimInterface;
}

// CANDIDATE_DOCUMENTS
export interface GetCandidateDocumentsAction {
  type: ActionTypes.GET_CANDIDATES_DOCUMENTS;
  payload: DocumentClaimUser[];
}

// CLAIM TYPES =====================================
export interface GetClaimingTypesAction {
  type: ActionTypes.GET_CLAIMING_TYPES;
  payload: ClaimTypesInterfaceAPI[];
}

// SET CLAIM TYPE =====================================
export interface setSelectedClaimTypeActions {
  type: ActionTypes.SELECT_CLAIM_TYPE;
  payload: PlansTypeKeys | "all";
}

// MY PERSONAL CLAIMS
export interface GetMyClaimsAction {
  type: ActionTypes.GET_MY_CLAIMS;
  payload: CandidateClaimInterface[];
}

// ALL CANDIDATES CLAIMS
export interface GetAllClaimsAction {
  type: ActionTypes.GET_ALL_CLAIMS;
  payload: CandidateClaimInterface[];
}

// GET ALL APPLICATIONS BY PERIOD
export interface GetAllApplicationsAction {
  type: ActionTypes.GET_ALL_APPLICATION;
  payload: allClaimApplicationInterface[];
}

export type ClaimingActionType =
  | GetCandidateApplicationsAction
  | GetCandidateDocumentsAction
  | GetClaimingTypesAction
  | setSelectedClaimTypeActions
  | CreateClaimAction
  | GetMyClaimsAction
  | GetAllClaimsAction
  | EvaluateClaimAction
  | CreateReClaimAction
  | GetAllApplicationsAction;

/**
 * * ****************************** ACTIONS *****************************
 */

// CLAIM TYPES ==========================
export const GetClaimingTypes = (callback: Function) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<ClaimTypesInterfaceAPI[]>(
        `${API.CLAIMING}/claim-type`
      );
      console.log({ claimtYPES: res.data });
      let claim_types: ClaimTypesInterfaceAPI[] = [];
      if (res.status === 200) {
        // setApplications(res.data);
        if (res.data !== null && res.data.length > 0) {
          claim_types = res.data;
        }
      }
      dispatch<GetClaimingTypesAction>({
        type: ActionTypes.GET_CLAIMING_TYPES,
        payload: claim_types,
      });
      callback(false, "");
    } catch (error) {
      console.error({ error: errorToText(error) });
      callback(false, errorToText(error));
    }
  };
};

// SELECT CLAIM TYPES ==========================
export const setSelectedClaimType = (
  thisType: PlansTypeKeys | "all"
): setSelectedClaimTypeActions => {
  console.log({ thisType });
  return {
    type: ActionTypes.SELECT_CLAIM_TYPE,
    payload: thisType,
  };
};

// CREATE PERSONAL CLAIM
export const createClaim = (data: CandidateClaimInterface) => {
  return {
    type: ActionTypes.CREATE_CLAIM,
    payload: data,
  };
};

// CREATE PERSONAL RECLAIM
export const createReClaim = (data: CandidateClaimInterface) => {
  return {
    type: ActionTypes.RE_CLAIM,
    payload: data,
  };
};

// EVALUATE CANDIDATE CLAIM
export const evaluateClaim = (data: CandidateClaimInterface) => {
  return {
    type: ActionTypes.EVALUATE_CLAIM,
    payload: data,
  };
};

// MY PERSONAL CLAIMS
export const getMyClaims = (
  user_id: string,
  period_id: string,
  callback: Function,
  order: "desc" | "asc" = "desc",
  sortBy: "createdAt" | "last_claim_status" = "createdAt"
) => {
  console.log({ user_id, period_id });
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.post<CandidateClaimInterface[]>(
        `${API.CLAIMING}/user`,
        {
          order,
          sortBy,
          filter: {
            user: user_id,
            period_id,
          },
        }
      );
      console.log({ api_claim: res.data });

      dispatch<GetMyClaimsAction>({
        type: ActionTypes.GET_MY_CLAIMS,
        payload: res.data,
      });
      callback(false, "", res.data);
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};

// PERSONAL APPLICATION
export const getMyRejectedApplications = (
  user_id: string,
  callback: Function
) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<claimApplicationsInterface[]>(
        `${API.APPLICATION}/application/${user_id}`
      );
      console.log({ claimPersonalApplication: res.data });
      let application: claimApplicationsInterface[] = res.data;
      dispatch<GetCandidateApplicationsAction>({
        type: ActionTypes.GET_CANDIDATES_APPLICATIONS,
        payload: application,
      });
      callback(false, "", res.data);
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};

// PERSONAL APPLICATION
export const getMyRejectedDocuments = (callback: Function) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<{
        n_id: string;
        fname: string;
        lname: string;
        district: string;
        phone_number: string;
        email: string;
        details: DocumentClaimUser[];
      }>(`${API.AUTH}/currentdet`);
      console.log({ claimPersonalDocuments: res.data });
      let details: DocumentClaimUser[] = [];
      if (res.status === 200) {
        // setApplications(res.data);
        if (res.data !== null && res.data.details.length > 0) {
          let dataRejected: DocumentClaimUser[] = [];
          for (const key in res.data.details) {
            if (res.data.details[key].validation_status !== "waiting") {
              dataRejected.push(res.data.details[key]);
            }
          }
          details = dataRejected;
        }
      }
      console.log({ claimPersonalDocumentDetails: details });

      dispatch<GetCandidateDocumentsAction>({
        type: ActionTypes.GET_CANDIDATES_DOCUMENTS,
        payload: details,
      });
      callback(false, "", details);
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};

