import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../../actions";
import { DocumentClaimUser } from "../../../actions/claiming";
import { fetchUserAccess } from "../../../actions/users";
import { StoreState } from "../../../reducers";
import { DocumentReducerInterface } from "../../../reducers/document";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import { UserInterface } from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { getUserDocuments } from "../Admin";
import { ClaimGetDocTitle } from "./ClaimForm";

type DocFormViewProps = {
  thisDoc: DocumentClaimUser;
  setChooseClaimFile: (value: boolean) => void;
  setClaimFileId: (value: string) => void;
  setViewThisFile: (value: string) => void;
  documentCategories: DocumentReducerInterface;
  userList: UserInterface[];
  fetchUserAccess: Function;
  PushPopupAlert: Function;
};

const DocFormView: React.FC<DocFormViewProps> = (props) => {
  const [loadingUsers, setLoadingUsers] = useState(true);

  useEffect(() => {
    if (!props.userList.length && loadingUsers) {
      setLoadingUsers(false);
      props.fetchUserAccess();
    } else if (loadingUsers) {
      setLoadingUsers(false);
    }
  }, [loadingUsers, props]);

  return (
    <section className="">
      <div className="w-full bg-gray-100 px-4 py-1 animate__animated animate__fadeIn">
        <div className="flex flex-col mb-2">
          <div className="flex items-center justify-between">
            <span className="text-blue-600 font-bold text-xl">
              {ClaimGetDocTitle(props.documentCategories, props.thisDoc).main}
            </span>
            <button
              type="button"
              className="btn btn-blue"
              onClick={() => {
                props.setChooseClaimFile(false);
                props.setClaimFileId(props.thisDoc.doc_id);
                props.setViewThisFile("");
              }}
            >
              Choose
            </button>
          </div>
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Status:</span>
            <span className="font-bold text-yellow-900 rounded px-3 py-1">
              {capitalizeFirstLetter(props.thisDoc.validation_status)}
            </span>
          </div>
          <div className="flex items-center gap-2 text-sm text-gray-600">
            <span className={`italic`}>Evaluated by:</span>
            <span className="font-semibold">
              {loadingUsers ? (
                <div className="w-24 h-3 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
              ) : getUserDocuments(
                  props.userList,
                  props.thisDoc.validated_by.user_id
                ) ? (
                getUserDocuments(
                  props.userList,
                  props.thisDoc.validated_by.user_id
                )!.user.user_info.fname +
                " " +
                getUserDocuments(
                  props.userList,
                  props.thisDoc.validated_by.user_id
                )!.user.user_info.lname
              ) : (
                "-"
              )}
            </span>
          </div>
          <div className="flex items-start gap-2 text-sm text-gray-600">
            <span className={`italic`}>Reason:</span>
            <span className="font-normal">
              {props.thisDoc.validated_by.comment}
            </span>
          </div>
          <div className="w-full my-1">
            <img
              src={`${API.AUTH_FILES}/${props.thisDoc.files}`}
              alt="doc file"
              className={`w-full`}
            />
          </div>
        </div>
      </div>
    </section>
  );
};

const mapStateToProps = ({
  users,
}: StoreState): {
  userList: UserInterface[];
} => {
  return {
    userList: users,
  };
};

export default connect(mapStateToProps, { PushPopupAlert, fetchUserAccess })(
  DocFormView
);
